}
body.openMenuMobile .header-hambuger span:nth-child(2) {
  @apply opacity-0;
}
body.openMenuMobile .header-hambuger span:nth-child(3) {
  rotate: -45deg;
  translate: 0.2083333333rem -0.0520833333rem;
}